import React, {useState} from 'react';
import Style from './Navbar.module.scss';
import Toggler from "./home/Toggler";
import {Link, useLocation} from "react-router-dom";
import {Box} from "@mui/material";
import {info} from "../info/Info";

const left_links = [
    {
        name: info.initials,
        type: 'initials',
        to: '/',
        active: 'home'
    }
]

const links = [
    {
        name: 'Home',
        to: '/',
        active: 'home'
    },
    {
        name: 'About Me',
        to: '/about',
        active: 'about'
    },
    {
        name: 'Portfolio',
        to: '/portfolio',
        active: 'portfolio'
    }
]

export default function Navbar({darkMode, handleClick}) {
    const location = useLocation()
    const [active, setActive] = useState(location.pathname === '/' ? 'home' : location.pathname.slice(1, location.pathname.length));
    const navbarBackgroundColor = (left_links[0].active !== active) ? (darkMode ? Style.navbarActiveDark : Style.navbarActive) : undefined

    return (
        <Box component={'nav'} width={'100%'}>
            <Box component={'ul'} display={'flex'} className={navbarBackgroundColor}>
                <Box component={'ul'} display={'flex'} justifyContent={'left'} alignItems={'center'}
                    gap={{xs: '2rem', md: '8rem'}} sx={{width: "50%", mx: "10%"}}
                    textTransform={'uppercase'} fontSize={'1rem'}>
                    {left_links.map((link, index) => (
                        <Box key={index} component={'li'} className={(link.active === active && !link.type) && Style.active}
                            sx={{borderImageSource: info.gradient}}>
                            <Link to={link.to} onClick={() => setActive(link.active)} className={Style.link}>
                                {!link.type && <p style={{padding: '0.5rem 0', whiteSpace: "nowrap"}}>{link.name}</p>}
                                {link.type && <h1>{link.name}</h1>}
                            </Link>
                        </Box>
                    ))}
                </Box>
                <Box component={'ul'} display={'flex'} justifyContent={'right'} alignItems={'center'}
                    gap={{xs: '2rem', md: '6rem'}} sx={{width: "50%", mx: "10%"}}
                    textTransform={'uppercase'} fontSize={'1rem'}>
                    {links.map((link, index) => (
                        <Box key={index} component={'li'} className={(link.active === active && !link.type) && Style.active}
                            sx={{borderImageSource: info.gradient}}>
                            <Link to={link.to} onClick={() => setActive(link.active)} className={Style.link}>
                                {!link.type && <p style={{padding: '0.5rem 0', whiteSpace: "nowrap"}}>{link.name}</p>}
                                {link.type && <h1>{link.name}</h1>}
                            </Link>
                        </Box>
                    ))}
                    <li>
                        <Toggler darkMode={darkMode} handleClick={handleClick}/>
                    </li>
                </Box>
            </Box>
        </Box>
    )
}